import axios from 'axios'
import orderList from '../../../components/orderList/orderList.vue'
import { mapGetters } from 'vuex'
export default {
  name:'myOrderAll',
  data(){
    return{

    }
  },
  components: {
    orderList
  },
  methods:{

  },
  mounted:function(){
    axios.get('http://'+this.getServerIp+'/orderList')
      .then(res =>{
          console.log(res.data)
      })
      .catch(error =>{
          console.log(error)
      })
  },
  computed:{
    ...mapGetters([
      'getServerIp'
    ])
  }
}



